<template>
  <div class="Calendar">
      <div class="date-container" v-touch:swipe.left="swipeleft" v-touch:swipe.right="swiperight">
        <span class="week-name  week-block" v-for="name in weekday" :key="name">{{name}}</span>
      <div class="day-container" :class="foldClass">
        <div class="week-day week-block" v-for="i in firstDay" :key="i"></div>
        <div class="week-day week-block" v-for="i in monthD" :key="i + 7">
          <span class="day" :class="{'chosen-day': i==chosenD, 'current-day':i==currentD&&thisMonth, 'foldup': foldup, 'hasthing': stateSet.has(i)}" v-touch:tap="chooseDay">{{i}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'calendar',
  data() {
      return {
          weekday: ['日','一','二','三','四','五','六'],
          currentY: 2019,
          currentM: 1,
          currentD: 1,
          chosenY: 1,
          chosenM: 1,
          chosenD: 1,
          showW: 1,
      }
  },
  computed: {
    firstDay: function() {
      return new Date(`${this.chosenY}-${this.chosenM}-1`).getDay();
    },
    thisMonth: function() {
      return this.currentY == this.chosenY && this.currentM == this.chosenM;
    },
    monthD: function() {
      return new Date(this.chosenY, this.chosenM, 0).getDate();
    },
    week: function() {
      switch(this.showW) {
        case 1: return 'week1';
        case 2: return 'week2';
        case 3: return 'week3';
        case 4: return 'week4';
        case 5: return 'week5';
        case 6: return 'week6';
      }
      return '';
    },
    maxWeek: function() {
      return Math.floor((this.firstDay + this.monthD - 1) / 7) + 1;
    },
    foldClass: function() {
      return {
        'foldup': this.foldup,
        [this.week]: this.foldup,
      }
    },
    stateSet: function() {
      return new Set(this.dayState);
    }
  },
  methods: {
      initdata() {
          let date = new Date();
          this.currentY = date.getFullYear();
          this.currentM = date.getMonth() + 1;
          this.currentD = date.getDate();
          this.chosenY = date.getFullYear();
          this.chosenM = date.getMonth() + 1;
          this.chosenD = date.getDate();
          this.showW = Math.floor((this.firstDay + 15) / 7) + 1;
          this.$emit('chooseDate', {year: this.chosenY, month: this.chosenM, day: this.chosenD});
          //this.firstDay = this.getDay(this.currentY, this.currentM, 1);
      },
      getDay(year, month, day) {
        return new Date(`${year}-${month}-${day}`).getDay();
      },
      swiperight() {
        if(!this.foldup) {
          this.chosenD = 1;
          this.chosenM--;
        } else {
          this.showW--;
        }
      },
      swipeleft() {
        if(!this.foldup) {
          this.chosenD = 1;
          this.chosenM++;
        } else {
          this.showW++;
        }
      },
      chooseDay(e) {
        this.chosenD = e.target.textContent;
        this.emitdate();
      },
      emitdate() {
        this.$emit('chooseDate', {year: this.chosenY, month: this.chosenM, day: this.chosenD});
      },
      toToday() {
        this.chosenY = this.currentY;
        this.chosenM = this.currentM;
        this.chosenD = this.currentD;
      }
  },
  props: {
    foldup: {type: Boolean, default: true},
    dayState: {type: Array, default: function() {return [5]}},
  },
  watch: {
    chosenM: function(val) {
      if(val > 12) {
        this.chosenM = 1;
        this.chosenY++;
      }
      if(val < 1) {
        this.chosenM = 12;
        this.chosenY--;
      }
      this.emitdate();
    },
    chosenD: function() {
      this.emitdate();
    },
    showW: function(val) {
      if(val > this.maxWeek) {
        this.chosenM++;
        this.showW = 1;
        this.chosenD = 1;
      }
      if(val < 1) {
        this.chosenM--;
        this.showW = this.maxWeek;
        this.chosenD = 1;
      }
    },
  },
  created() {
      this.initdata();
  },
  beforeUpdate() {
  },
}
</script>

