        return {
            weekday: ['日','一','二','三','四','五','六'],
            currentY: 2019,
            currentM: 1,
            currentD: 1,
            choosenY: 1,
            choosenM: 1,
            choosenD: 1,
            foldup: true,
            dayState: [],
            things: [
                {
                    name: '227市场',
                    discrip: '定价依据和客户判定',
                    role: 1,
                    time: '16: 24'
                },
                {
                    name: '新软件',
                    discrip: '成立体验小组, 测试软件',
                    role: 0,
                    time: '13: 34'
                },
                {
                    name: '2-3-6手提项目',
                    discrip: '236手提灭火器项目，十月需要提交给需求方',
                    role: 0,
                    time: '09: 24'
                },
                {
                    name: '消防资格考试',
                    discrip: '考试纲要 考试时间 考试地点',
                    role: 1,
                    time: '昨天'
                },
                {
                    name: '消防资格考试',
                    discrip: '考试纲要 考试时间 考试地点',
                    role: 1,
                    time: '昨天'
                },
                {
                    name: '消防资格考试',
                    discrip: '考试纲要 考试时间 考试地点',
                    role: 1,
                    time: '昨天'
                }
            ]
        }
    },
    computed: {
        pulldir: function() {
            return this.foldup?'to-bottom':'to-up'
        },
    },
    methods: {
        initdata() {
            let date = new Date();
            this.currentY = date.getYear() + 1900;
            this.currentM = date.getMonth() + 1;
            this.currentD = date.getDate();
            this.choosenY = date.getYear();
            this.choosenM = date.getMonth() + 1;
            this.choosenD = date.getDate();
            this.getState();
        },
        chooseDate(date) {
            ({'year': this.choosenY, 'month': this.choosenM, 'day':this.choosenD} = date);
        },
        getState() {
            this.$http.get('https://nei.netease.com/api/apimock/b32ba6d6d7cdacdcb5ac226ca12b4c4a/')
                .then(res => {
                    this.dayState = res.data.state;
                });
        },
        closeOrOpen() {
            this.foldup = !this.foldup;
        },
        toToday() {
            this.$refs['calendar'].toToday();
        }
    },
    components: {
        CalendarBar
    },
    created() {
        this.initdata();
    },
}
</script>
